.animate-cube{
  animation: rotate 500s infinite;
}
@keyframes rotate {
  0% {
    transform: rotateZ(0deg)
  }
  100% {
    transform: rotateZ(3600deg)
  }
  0% {
    transform: rotateZ(0deg)
  }
}
.animate-globe{
  animation: rotateGlobe 500s infinite;
}
@keyframes rotateGlobe {
  0% {
    transform: rotateZ(0deg)
  }
  100% {
    transform: rotateZ(3600deg)
  }
  0% {
    transform: rotateZ(0deg)
  }
}
.animate-cube-2{
  animation: rotate2 500s infinite;
}
@keyframes rotate2 {
  0% {
    transform: rotateZ(-3600deg)
  }
  100% {
    transform: rotateZ(0deg)
  }
 
}
