@import './vars';
.hero{
  position: relative;
  overflow: hidden;
  padding-bottom: 90px;
    header{
      width: 100%;
      padding: 50px 0px 0px 0px;
      margin-bottom: 85px;
      z-index: 1000;
      position: relative;
      .web-container{
        max-width: 1375px;
      }
      /* row style */
      .row{
        align-items: center;
        *:not(.dn){
          display: flex;
          align-items: center; 
        }
      }
      /* header items  */
      .logo{
        //margin-right: auto;
        img{
          width: 100%;
          min-width: 125px;
        }
      }
      .web-btn{
        width: 210px;
        height: 72px;
        padding: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 16px;
        font-weight: 500;
        background: var(--yellow);
      }
      nav{
        margin-right: auto;
        .nav{
          &-item:not(:last-child){
            margin-right: 51px;
          }
          &-item{
            color: #999FAE;
          }
          
          &-item:hover{
            color: #292930;
          }
        }
      }
      .connect{
        justify-content: space-evenly;
      }
    }
    .sub-header{
      margin-bottom: 120px;
      .headline{
        h1{
          margin-bottom: 45px;
          font-size: 76px;
          line-height: 86px;
        }  
        span{
          color:  #5956E9;
        }
        &-sub-title{
          font-size: 25px;
          margin-bottom: 55px;
        }
      }
      .phone-block{
        position: relative;
        max-width: 230px;
      } 
      /* call to action block */
      .headline-buttons{
        display: flex;
        align-content: center;
        margin-bottom: 80px;
        .web-btn{
          background-color: var(--blue);
          margin-right: 25px;
        }
      }
      .headline-scrolling{
        display: inline-block;
        position: relative;
        margin-bottom: 150px;
        span{
          padding-left: 26px;
        }
        &:before{
          content: url(../img/scroll-horitontal.svg);
          position: absolute;
          width: 24px;
          height: 24px;
        }
      }
    }
    /* partners block */
  &-partners{
    &--boxed{
      width: 100%;
      display: flex;
      justify-content: space-evenly;
      flex: 1 1 auto;
      text-align: center;
      color: var(--dim-color);
      font-weight: var(--light-font);
      font-size: 19px;
    }
   &-item{
     flex-basis: 21%;
   }
  }
}
