/* clients section styles */
@import'./vars';
@import'./_shapes.scss';
.clients{
  &-shapes{
    div{
      background: #525260;
    }
  }
  padding: 100px 0 60px 0;
  background-color: #27272E;
  &-title-block{
    display: flex;
    justify-content: flex-end;
    width: 100%;
    margin-bottom: 60px;
    z-index: 200;
    font-weight: var(--bold-font);
    color: var(--white);
    .title-block__text{
      flex-basis: 71%;
     
      h2{
        margin-bottom: 50px;
      }
    }
    .title-block__sub-text{
      color: var(--white);
      opacity: 0.7;
    }   
    
  }
  &-list{
    display: flex;
    flex-wrap: wrap;
    flex: 1 1 auto;
    &__item{
      flex-basis: 25%;
      padding: 45px 40px;
      text-align: center;
      opacity: 0.2;
    }
  }
}