/* Contact section */
@import './vars';
@import'./_shapes';
.contact{
  padding-top: 200px;
  &__box{
    position: relative;
    z-index: 100000;
    display: flex;
    background-color: var(--white);
    padding: 160px 70px 10px;
    border-top-left-radius: 44px;
    border-top-right-radius: 44px;
  
  }
  .contact__left-side{

    .left-side__header{
      .header__tag{
        font-weight: 700;
        font-size: 20px;
        line-height: 28px;
        margin-bottom: 24px;
        color: var(--blue);
      }
     .header__title{
       margin-bottom: 48px;
     }
     .header__sub-title{
      font-size: 18px;
      line-height: 32px;
      margin-bottom: 85px;
     }
    }
  }
  .info__list{
    padding-right: 45%;
    .list__item{
      margin-bottom: 40px;
      a{
        display: flex;
        align-items: center;
        font-size: 18px;
        line-height: 32px;
        color: #111029;
      }
      span{
        display: inline-block;
      }
      img{
        margin-right: 45px;
      }
    }
  }
  .contact__right-side{
    padding-top: 440px;
    .graphic{
      position: absolute;
      top: -360px;
      left: -40px;
      /* margin-bottom: 160px; */
    }
    .graphic-img{
     max-width: 700px;
    }
    .pink-cube{
      position: absolute;
      left: 85px;
      top: 250px;
    }
  }
  .contact-form{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-left: 30px;
    input, textarea, .drop-title{
      width: 100%;
      position: relative;
      padding: 20px 24px;
      background-color: #f4f4f48f;
      border-radius: 20px;
      font-family: var(--main-font)!important;
       border: 2px solid transparent;
      &:active, &:focus{
        background-color: transparent;
        border: 2px solid var(--blue);
        box-shadow: 0px 8px 24px rgba(204, 204, 204, 0.5);
      }
    }
    label{
      position: relative;
      display: inline-flex;
      width: 100%;
      margin-bottom: 24px;
      &:after{
        position: absolute;
        width: 17px;
        height: 17px;
        right: 20px;
        top: calc(50% - 13.5px);
      }
      &:first-child:after{
        content: url(../img/user-icon-label.svg);
      }
      &:nth-child(2):after{
        content: url(../img/send-message.svg);
      }
      &:last-child:after{
        content: url(../img/f-chat.svg);
        top: 20px;
      }
    }
    .drop-title{
      color: var(--dim);
      margin-bottom: 24px;
      span{
        position: absolute;
        right: 20px;
        display: inline-flex;
        flex-direction: column;
        top: 25px;
      }
    }
    .action-btn{
      background-color: var(--blue);
      max-width: 240px;
      padding: 15px 65px 15px 30px;
      border-radius: 15px;
      font-size: 18px;
      &:after{
        content: url(../img/arrow-right-bold.svg);
        position: absolute;
        right: 20px;
        top: calc(50% - 7px);
      }
    }
  }
  &__footer{
    width: 100%;
    background-color: var(--white);
    padding: 90px 0px 50px 0px;
    border-bottom-left-radius: 44px;
    border-bottom-right-radius: 44px;
    position: relative;
    .footer__list{
      display: flex;
      justify-content: space-evenly;
    }
    .list__item{
      a{
        position: relative;
        padding: 40px 5vw;
        border-radius: 24px;
        opacity: .4;
        &:hover{
          opacity: 1;
          box-shadow: 1px 17px 14px 0px #b3b2b278;
        }
        &:first-child{
          &:before{
            content: url(../img/Instagram-icon.svg);
            position: absolute;
            width: 17px;
            height: 17px;
            left: 35px;
            top: calc(50% - 7.5px);
          }
        }
      }
    }
    &:after{
      content: '';
      position: absolute;
      bottom: -17px;
      width: 95%;
      height: 20px;
      border-bottom-left-radius: 44px;
      border-bottom-right-radius: 44px;
      background-color: #203696;
      left: 2.5%;
      z-index: 1000;
    }
     &:before{
      content: '';
      position: absolute;
      bottom: -30px;
      width: 90%;
      height: 20px;
      border-bottom-left-radius: 44px;
      border-bottom-right-radius: 44px;
      background-color: #1B1B87;
      left: 5%;
      z-index: 1000;
    }
  }
  form{
    width: 100%;
    font-family: var(--main-font);
  }
  .container{
    position: relative;
    z-index: 10000;
  }
}
