body{
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  box-sizing: border-box;
  color: var(--primary-font-color);
  font-family: var(--main-font), sans-serif;
  font-weight: var(--regular-font);
  transition: ease-in 0.2s all;
  a{
    color: inherit;
   
    &:hover{
      color: inherit;
      text-decoration: none;
    }
  }
  a, .web-btn, .action-btn, .btn-video>img, .blog__swiper-button-prev, .blog__swiper-button-next{
    cursor: pointer;
  }
  .showcase, .start-up, .banner, .blog, .contact{
   background-color: #e5e5e5;
  }
}


