@import'./vars';
.shapes-container{
  position: relative;
  width: 100%;
  height: 100%;
  .shape{
    position: absolute;
    border-radius: 50%;
  }
  /* hero section shapes */
  .circle-1{
    width: 400px;
    height: 400px;
    left: -200px;
    top: -200px;
    background: var(--yellow);
  }
  .circle-2{
    width: 60%;
    max-width: 1000px;
    height: 1029px;
    right: -170px;
    top: -100px;
    background: #5956E9;
    &-inner-1{
      position: relative;
      width: 150px;
      height: 150px;
      top: 35%;
      left: -25px;
      background-color: var(--yellow);
    }
    &-inner-2{
      width: 115px;
      height: 115px;
      right: 51%;
      top: 650px;
      background: #FFFFFF;
      mix-blend-mode: normal;
      opacity: 0.1;
      border: 1px solid #FFFFFF;
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    }
    &-inner-3{
      width: 33px;
      height: 33px;
      right: 25%;
      top: 85%;
      background: #FFFFFF;
      mix-blend-mode: normal;
      opacity: 0.1;
    }
    &-inner-4{
      background-color: var(--blue);
      left: -5%;
      bottom: 0%;
    }
    &-inner-5{
      background-color: var(--pink);
      width: 115px;
      height: 115px;
      left: -19%;
      bottom: -20%;
      opacity: 0.3;
    }
    &-inner-6{
      background-color: var(--blue);
      width: 32px;
      height: 32px;
      bottom: 0%;
      left: -5%;
    }
  }
 
  .circle-3{
    top: 300px;
    left: 67px;
    background-color: var(--pink);
  }
 
 
  /* hero phone  */
  .phone-box{
    max-width: 245px;
    width: 30%;
    top: 45%;
    left: 5%;
    .hero-phone{
      width: 100%;
      position: relative;
      z-index: 100;
    }
    &:before{
      content: '';
      position: absolute;
      width: 90%;
      bottom: 15px;
      box-shadow: 4px 6px 20px 4px #8c8686;
      left: 8%;
      z-index: 10;
    }
    .phone-msg{
      max-width: 355px;
      position: absolute;
      left: -155px;
      z-index: 100;
      bottom: 8%;
    }
    .phone__inner{
      max-width: 250px;
      position: absolute;
      top: -89px;
      right: 20px;
      img{
        position: absolute;
      }
      .popUp__back{
        left: -35px;
      }
      .popUp__front{
        top: -12px;
      }
    }
  }
  /* clients section shapes */
  .clients-shapes__item-1{
    width: 520px;
    height: 520px;
    border: 1px solid #42424A;
    background: transparent;
    left: 15%;
    top: -99px;
    .clients-shapes__tag{
      position: absolute;
      font-size: 20px;
      line-height: 20px;
      left: 7%;
      top: 14%;
      color: var(--white);
    }
    &__inner-1{
      width: 40px;
      height: 40px;
      mix-blend-mode: normal;
      opacity: 0.5;
      top: 7%;
      left: 35%;
    }
  }
  .clients-shapes__item-2{
    position: absolute;
    width: 268px;
    height: 268px;
    mix-blend-mode: normal;
    opacity: 0.7;
    right: 9%;
    top: -50px;
  }
  .clients-shapes__item-3{
    z-index: 100;
    width: 90px;
    height: 90px;
    mix-blend-mode: normal;
    opacity: 0.7;
    transform: rotate(-45deg);
    left: 4%;
    top: 480px;
    &::before{
      content: '';
      position: absolute;
      width: 650%;
      height: 1px;
      background-color: #fff;
      top: 50%;
      left: -120%;
      opacity: .3;
      z-index: 10;
    }
  }
  /* showcase shapes */
  .showcase-shape-1{
    width: 190px;
    height: 190px;
    left: -90px;
    top: 80px;
    background-color: var(--pink);
 }
  .showcase-shape-3{
    width: 60%;
    max-width: 820px;
    height: 820px;
    background-color: var(--blue);
    right: -50px;
    &__inner-1{
      width: 160px;
      height: 160px;
      background-color: var(--yellow);
      top: 45%;
      left: 10%;
    }
    &__inner-2{
      width: 48px;
      height: 48px;
      background: #FFFFFF;
      opacity: 0.3;
      right: 170px;
      top: 20%;
    }
    &__inner-3{
      right: 80px;
      top: 17%;
      img{
        width: 120%;
      }
    }
    &__inner-4{
      width: 24px;
      height: 24px;
      right: 80px;
      top: 64%;
      background-color: var(--white);
    }
  }
  /* features  */
  .features-shapes__item-1{
    width: 150px;
    height: 150px;
    background-color: var(--yellow);
    top: -25px;
    left: 100px;
  }
  .features-shapes__item-2{
    width: 121px;
    height: 121px;
    background-color: var(--blue);
    top: -75px;
    right: 0;
  }
  .features-shapes__item-3{
    width: 70px;
    height: 70px;
    background-color: var(--pink);
    left: calc(50% - 35px);
    top: 120px;
  }
  .lamp-shape{
    border-radius: 0 !important;
  }
  .features-shapes__item-4{
    max-width: 220px;
    right: 50px;
    top: 125px;
    img{
      width: 100%;
    }
  }
  .features-shapes__item-5{
    width: 40px;
    height: 40px;
    background-color: var(--yellow);
    right: 5px;
    top: 280px;
  }
  .features-shapes__item-6{
    width: 140px;
    height: 140px;
    background-color: var(--blue);
    left: 30px;
    top: 500px;
  }
  /* incubation section */
  .incubation-shapes-1{
    right: 50px;
    top: -50px;
    max-width: 20%;
    img{
      width: 100%;
    }
  }
  .incubation-shapes-2{
    width: 204px;
    height: 204px;
    background-color: var(--blue);
    left: 35%;
    top: 259px;
    z-index: 50;
    .shapes-2__inner-1{
      position: absolute;
      right: -30px;
      bottom: -40px;

    }
  }
  .incubation-shapes-3{
    width: 95px;
    height: 95px;
    background-color: var(--white);
    top: 500px;
    left: 24%;
  }
  .incubation-shapes-4{
    width: 40px;
    height: 40px;
    background-color: var(--white);
    left: 12%;
    top: 650px;
  }
  .incubation-shapes-5{
    width: 64px;
    height: 64px;
    background-color: var(--white);
    left: 40%;
    top: 750px;
  }
  .incubation-shapes-6{
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--white);
    width: 35%;
    max-width: 505px;
    background-color: var(--blue);
    right: 200px;
    top: calc(100% + 500px);
    z-index: 100;
    .inner-1{
      img{
        margin-bottom: 24px;
      }
      &__title{
        font-weight: 500;
        font-size: 22px;
        line-height: 33px;
        margin-bottom: 57px;
      }
      .before-line{
        padding-left: 40px;
        &::before{
          background-color: var(--white);
          visibility: visible;
        }
      }
    }
  }
  .incubation-shapes-7{
    width: 99vw;
    top: 355px;
    img{
      width: 100%;
      object-fit: cover;
    }
  }
  /* ==============================
  start-up-shapes
  */
  .start-up-shape-1{
    width: 127px;
    height: 127px;
    background: var(--blue);
    left: -60px;
    top: -150px;
  }
  .start-up-shape-2{
    width: 85px;
    height: 85px;
    background: var(--pink);
    right: 80px;
    top: 60px;
  }
  .start-up-shape-3{
    width: 176px;
    height: 176px;
    right: 60px;
    top: 640px;
    background-color: var(--yellow);
  }
  .start-up-shape-4{
    width: 35%;
    max-width: 348px;
    left: -150px;
    top: 860px;
    background-color: var(--pink);
    z-index: 100;
  }
  /* banner */
  .banner-shape-1{
    right: 11%;
    top: 325px;
    opacity: 0.7;
  }
  .banner-shape-2{
    width: 50px;
    height: 50px;
    border-radius: 10px;
    background-color: #5956E9;
    right: 50px;
    top: 500px;
    background-image: url(../img/small-blur-cube.png);
    background-position: center;
    background-size: cover;
    opacity: 0.7;
  }
  .banner-shape-3{
    width: 40px;
    height: 40px;
    border-radius: 0;
    background-color: #5956e9;
    right: 250px;
    top: 400px;
    background-image: url(../img/small-blur-cube.png);
    background-position: center;
    background-size: cover;
    opacity: 0.5;
  }
  /* global */
  .global-shape-1{
    max-width: 500px;
    width: 25%;
    background-color: var(--blue);
    left: -40px;
    top: -175px;
    z-index: 1000;
  }
  .global-shape-2{
    width: 205px;
    background-color: var(--blue);
    left: -18%;
    bottom: -55px;
    z-index: 10;
    height: 205px;
  } 
  .global__globe-img{
    width: 100%;
    max-width: 620px;
  }
  .global__clound-img-1{
   left: -90px;
  }
  .global__clound-img-2{
    top: -125px;
    right: 130px;
  }
  .global__clound-img-1, .global__clound-img-2{
    max-width: 260px;
    width: 38%;
  }
  /* blog */
  .blog-shape-1{
    width: 90px;
    height: 90px;
    background-color: var(--yellow);
    left: 36%;
    top: -40px;
  }
  .blog-shape-2{
    width: 328px;
    height: 328px;
    background-color: var(--pink);
    right: 20%;
    top: 100px;
  }
  /* contact */
  .contact-shape-2{ 
    background-image: url(../img/Line-Copy-6.svg);
    width: 500px;
    height: 560px;
    top: 65vh;
    left: 0;
    top: 65vh;
    border-radius: 0px;
    background-repeat: no-repeat;
    background-size: cover;
  }
  
  /* footer */
  .footer-shape-1, .footer-shape-2{
    width: 200px;
    height: 200px; 
    background-repeat: no-repeat;
    background-position: center;
    border-radius: 0px;
    opacity: 0.1;
  }
  .footer-shape-1{
    background-image: url(../img/cube-copy.png);
    left: 20%;
    top: -100px;
  }
  .footer-shape-2{
    background-image: url(../img/cube-copy-5.png);
    left: 50%;
    top: 300px;
  }
  
}
 @media (max-width: $breakPoint1200){
  .shapes-container {
    .circle-2{
     
    }
  }
   
}
@media (max-width: $tabletBreakpoint){
  .shapes-container {
    .incubation-shapes-2{
      left: 47%;
      top: 390px
    }
  }
}
