@import './vars';
@import'./_shapes';
.start-up{
  padding-top:20%;
  background-color: #E5E5E5;
  &__h3{
    margin-bottom: 35px;
  }
  .header{
    margin-bottom: 65px;
    &__tag{
      font-weight: bold;
      font-size: 20px;
      color: var(--red);
      margin-bottom: 24px;
    }
  }
  &__slider-nav{
    display: flex;
    justify-content: space-between;
    padding-right: 70%;
    z-index: 100;
  }
  &__swiper-button-prev,
  &__swiper-button-next{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    outline: none;
    background-color: var(--pink);
    cursor: pointer;
  }
  &__swiper-container{
   
    overflow: hidden;
    .swiper-slide{
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
    }
    .cart{
      display: flex;
      flex-direction: column;
      margin-bottom: 90px;
      flex-basis: 48%;
      padding-top: 65px;
      &__name{
        font-weight: bold;
        font-size: 20px;
        margin-bottom: 30px;
      }
      &__text{
        margin-bottom: 40px;
        padding-right: 10%;
      }
      &__footer{
        display: flex;
        align-items: center;
        
        img{
          margin-right: 20px;
        }
        .footer__name{
          display: flex;
          flex-direction: column;
          padding-top: 10px;
          font-size: 14px;
          line-height: 18px;
        }
      }
    }
    .cart-1{border-top: 2px solid var(--pink);}
    .cart-2{border-top: 2px solid var(--blue);}
    .cart-3{border-top: 2px solid var(--pink);}
    .cart-4{border-top: 2px solid var(--light-green);}
  }
}