/* global section */
@import './vars';
@import'./_shapes';
.global{
  background-color: var(--pink);
  padding-top: 100px;
  .content__global{
    position: relative;
    text-align: center;
    img{
      z-index: 1000;
    }
  }
  .global__cart{
    padding-top: 100px;
  }
  .cart{
    display: flex;
    flex-direction: column;
    padding: 45px 42px;
    border-radius: 44px;
    position: relative;
    background-color: var(--white);
    z-index: 1000;
    .before-line{
      color: var(--blue);
      padding-left: 40px;
      margin-bottom: 57px;
      &::before{
        background-color: var(--blue);
        visibility: visible;
      }
    }
    h2{
      margin-bottom: 30px;
    }
    p{
      margin-bottom: 50px;
    }
    .web-btn{
      background-color: var(--blue);
      color: var(--white);
      padding: 26px 30px;
    }
  /*   &:after{
      content: '';
     
      border-radius: 50%;
      position: absolute;
    } */
  }
}