/* features section */
@import './vars';
@import'./_shapes';
.features{
  padding-bottom: 70px;
  background-color: #e5e5e5;
  .container{
    padding-top: 325px;
   
  }
  &__tag{
    color: var(--blue);
  }
  &__title{
    margin-bottom: 116px;
    .title__text{
      margin-bottom: 30px;
    }
  }
  /* slider */
  .slide-content{
    max-width: 400px;
    padding: 30px;
    border-radius: 44px;
    &__img{
      margin-bottom: 38px;
      max-height: 76px;
    }
    &__title{
      font-size: 22px;
      color: #292930;
      margin-bottom: 35px;
    }
    &__text{
      font-size: 16px;
      line-height: 24px;
      margin-bottom: 15px;
    }
    &__sub-title{
      font-size: 16px;
      color: #292930;
    }
    
  }
  .swiper-slide{
    .slide-content{
      .slide-content__sub-title:not(.before-line){
        padding-left: 0;
      }
    }
  }
  /* active slide */
  .swiper-slide-active{
    max-width: 400px;
    .slide-content{
      .before-line{
        padding-left: 0;
        &:before{
          visibility: hidden;
        }
      }
    }
  }
  /* next slide */
  .swiper-slide-next{
    .slide-content{
      background-color: var(--white);
      .before-line{
        padding-left: 40px;
        &::before{
          visibility: visible;
          color: var(--blue);
        }
        
      }
    }
  }
  .slider-container{
    padding-top: 0;
  }
}
$breakpoint-tablet: 1500px;
@media (min-width: $breakpoint-tablet) {
  .features{
    .slider-container{
      max-width: 1300px!important;
      padding-top: 0;
    }
  }
}