/* incubation section */
@import './vars';
@import'./_shapes';
.incubation{
  padding: 140px 0px 500px 0px;
  background-color: var(--pink);
  &__content{
    .content__tag{
      font-weight: 700;
      color: #C75C6F;
    }
    .content__title{
      font-size: 134px;
    }
  }
  
}

@media screen and (max-width: $breakPoint992){
  .incubation{
    .content__title{
      font-size: 80px;
    }
  }
}