@import './vars';
@import'./_shapes';
footer{
  position: relative;
  background-color: #27272E;
  color: var(--white);
  padding: 200px 0px 115px 0px;
  &:before{
    content: url(../img/rectangle.svg);
    height: 200px;
    position: absolute;
    top: -620px;
    z-index: 100;
    right: 0;
  }
 
  .logo{
    margin-right: 100px;
    .logo__img{
      width: 69px;
    }
  }
  .footer__text{
    .text__title{
        font-size: 72px;
        line-height: 84px;
        margin-bottom: 48px;
    }
    .text_sub-title{
      font-size: 32px;
      color: #FFBDBD;
    }
  }
  .footer__left-side{
    display: flex;
  }
  .footer__right-side{
    display: flex;
    justify-content: flex-end;
  }   
  .right-side__list{
    .list__item{
      margin-bottom: 22px;
      &:nth-child(5){
        margin-bottom: 80px;
      }
      a{
        font-size: 18px;
        &:hover{
          opacity: .3;
        } 
      }
      &:last-child{
        font-size: 16px;
        opacity: .3;
      }
    }
    
  }
  
}
/* basement */
.footer__basement{
  position: relative;
  background-color: #27272E;
  color: var(--white);
  padding-top: 100px;
  &:after{
    content: '';
    position: absolute;
    height: 1px;
    width: 100%;
    background-color: #fff;
    opacity: .3;
    top: 0;
    left: 0;
  }
  .logo{
    margin-right: 0!important;
  }
  .basement__content{
    display: flex;
    justify-content: space-evenly;
    .content__list{
      .list__title{
        margin-bottom: 50px;
      }
      .list__item{
        margin-bottom: 20px;
        opacity: .3;
      }
    }
  }
}