/* showcase section styles */
@import'./vars';
@import'./_shapes';
.showcase{
  padding-top: 110px;
  overflow: hidden;
  &-content{
    .content__tag{
      color: var(--blue);
      margin-bottom: 24px;
    }
    .content__title{
      margin-bottom: 32px;
    }
    .content__sub-title{
      margin-bottom: 45px;
    }
    .web-btn{
      background-color: var(--blue);
    }
  }
  &-isometric-block{
    position: relative;
    max-height: 1000px;
    width: 100%;
    display: flex;
    justify-content: flex-end;
    //background-image: url(../img/isometric-image.png);
    .isometric-block__backGround{
      transform: rotate(-30deg);
      width: 100%;
      text-align: center;
      img{
        width: 100%;
        max-width: 1200px;
        object-fit: cover;
      }
    }
    
    
  }
}