/* blog section */
@import './vars';
@import'./_shapes';
.blog{
  padding: 100px 0px;
  &__header{
    margin-bottom: 100px;
    .tag{
      font-weight: 700;
      color: var(--blue);
      margin-bottom: 25px;
    }
  }
  .blog__slider{
    
    .blog__single-slide{
      display: flex;
      justify-content: space-between;
      transform-origin: left;
      transform: scaleX(0);
      transition: ease 1s;
      .slider__left-side, .slider__right-side{
        flex-basis: 48%;
        display: flex;
        flex-direction: column;
        img{
          width: 100%;
        }
      }
      .single-slide__content{
        padding: 35px 40px;
        background-color: var(--white);
        padding-left: 50px;
        border-bottom-left-radius: 40px;
        border-bottom-right-radius: 40px;
        .blog__slide-tag{
          margin-bottom: 21px;
        }
        .blog__slide-title{
          line-height: 30px;
          margin-bottom: 30px;
        }
        a{
          color: var(--blue);
          img{
            width: 6px;
            height: 12px;
          }
        }
      }
    }
    
  }
 
  .blog__slider-nav{
    display: flex;
    justify-content: flex-end;
    padding-top: 90px;
    .blog__swiper-button-prev, .blog__swiper-button-next{
      width: 30px;
      height: 32px;
      position: relative;
       &:after{
        content: '';
        position: absolute;
        width: 30px;
        height: 30px;
        border: 2px solid var(--blue);
        border-radius: 50%;
        top: -5px;
      }
      &:hover{
        &:after{
          visibility: visible;
        }
      }
    }
     .blog__swiper-button-prev{
       margin-right: 15px;
        &:after{
          left: 0;
          
          visibility: hidden;
        }
        
     }
     .blog__swiper-button-next{
        &:after{
          right: 0;
          visibility: hidden;
        }
     }
     .arrow-img{
        position: relative;
        fill: var(--dim);
        stroke:var(--dim);
        z-index: 1000;
        &:hover {
          fill:#000;
          stroke: #000;
         
        }
     }
  } 
  
  .swiper-slide-active, .swiper-slide-duplicate-active{
    transform: scaleX(1)!important;
  }
}