/* banner section */
@import './vars';
@import'./_shapes';
.banner{
  position: relative;
  z-index: 10;
  padding-bottom: 350px;
  &__box{
    background-image: url(../img/banner-bg.png);
    background-color: #5956E9;
    width: 100%;
    padding: 70px 10% 25%;
    border-radius: 40px;
    background-size: cover;
    background-position: center;
    .box__header{
      color: var(--white);
      text-align: center;
      .header__title{
        font-weight: bold;
        font-size: 80px;
        margin-bottom: 15px;
      }
      .header__sub-title{
        font-size: 18px;
        margin-bottom: 40px;
      }
      .web-btn{
        color: #2A288B;
        background-color: var(--white);
      }
    }
  }
  /* ===============
  Desktop images positions */
  &__desktop{
    position: absolute;
    left: calc(50% - 322.5px);
    bottom: 88px;
    .desktop__img{
      max-width: 645px;
      height: 450px;
      margin: 0 auto 20px;
      img{
        width: 100%;
        object-fit: cover;
      }
     
    }
    .desktop__chat-img{
      width: 78px;
      max-width: 78px;
      position: absolute;
      left: -140px;
      top: 66%;
      img{
        width: 100%;
        object-fit: cover;
      }
    }
    .desktop__msg-img{
      max-width: 250px;
      max-height: 117px;
      position: absolute;
      right: -115px;
      top: 20px;
      img{
        width: 100%;
        object-fit: cover;
      }
    }
    .desktop__keyboard-mouse-box{
      display: flex;
      justify-content: flex-end;
      align-items: center;
      .keyboard{
        max-width: 425px;
        width: 70%;
      }
    }
    .desktop__bell{
      max-width: 120px;
      position: absolute;
      right: -150px;
      top: 180px;
      img{
        width: 100%;
      }
    }
  }
}