:root{
  /* shapes color */
  --yellow: #FFDC60;
  --pink: #FAB8C4;
  --blue: #5956E9;
  --red: #C75C6F;
  --light-green: #90D0B1;
  --primary-font-color: #292930;
  --white: #fff;
  --dim: #979797;
  --main-font: 'Poppins';
  --regular-font: normal;
  --thin-font: 100;
  --light-font: 300;
  --medium-font: 500;
  --semi-bold-font: 600;
  --bold-font: bold;
  --extra-bold-font: 800;
  
}
$breakPoint1200: 1200px;
$tabletBreakpoint: 990px;
$breakPoint992: 992.2px;
ul,li{
  margin: 0;
  padding: 0;
}
li{
  list-style: none;
}
a{
  text-decoration: none;
}
p{
  margin: 0;
}
h1,h2,h3,h4{
  margin: 0;
  padding: 0;
}
h2{
  font-size: 80px;
  line-height: 100%;
}
h3{
  font-weight: 500;
  font-size: 40px;
}
h4{
  font-size: 32px;
}
/* hide or show classes */
.dn{
 display: none;
}
.opac1{
  opacity: 1!important;
}
.overflow-hidden{
  overflow: hidden;
}
/* Links classes */
.hovered-link{
  color: var(--primary-font-color);
}
/* Fonts classes */
.extra-bold{
  font-weight: 800;
}
.dim{
  color: #979797!important;
}
.tag{
  font-size: 20px;
}
/* Cirlces shapes  */
.small-circle{
  width: 32px;
  height: 32px;
}
/* buttons */
.web-btn{   
  display: inline-block;
  text-align: center;
  position: relative;
  z-index: 100000!important;
  padding: 29px 46px;
  border-radius: 20px;
  color: #fff;   
  &--radius{
    border-radius: 20px;
  }
}
button, input, textarea, .blog__swiper-button-prev, .blog__swiper-button-next{
  outline: none;
  border: none;
  border: none;
  &:hover{
    outline: none;
    border: none;
  }
  &:focus{
    outline: none;
    border: none;
  }
}
/* pseudo-classes */
.before-line{
  position: relative;
  padding-left: 40px;
  &:before{
    position: absolute;
    //visibility: hidden;
    content: '';
    width: 30px;
    height: 2px;
    top: -webkit-calc(50% - 1px);
    top: calc(50% - 1px);
    left: 0;
    background-color: var(--blue);
  }
}
/* big dot  */
.big-dot{
 color: var(--blue);       
}
/* svg */
svg path {
  fill:inherit;
  stroke:inherit;
  stroke-width:inherit;
  }
